import {mapState,mapMutations} from 'vuex'	
export default{
	data(){
		return{
			mydata:{
				originPass:'',
				password:'',
				surepassword:'',
			},
			sessionId:''
		}
	},
	computed: mapState(['hasLogin','uerInfo']),  
	methods:{
		...mapMutations(['login']),
		// 修改密码
		sure(){
			var reg = /^\d{6}$/
			if(this.mydata.password != this.mydata.surepassword){
				uni.showToast({
					title:'密码不一致',
					icon:'none'
				})
			}else if(!this.mydata.password || !this.mydata.surepassword || !this.mydata.originPass){
				uni.showToast({
					title:'请补全信息',
					icon:'none'
				})
			}else if(!reg.test(this.mydata.password) || !reg.test(this.mydata.surepassword)){
				uni.showToast({
					title:'请输入6位数的密码',
					icon:'none'
				})
			}else{
				uni.getStorage({
					key: 'userInfo',
					success:(res) => {
						this.login(res.data);
						this.sessionId = res.data.sessionId
						uni.request({
							url:'https://www.nssnail.com/wx/personal/updateUserPayPwd',
							method:'POST',
							data:{
								sessionId:this.sessionId,
								newPwd:this.mydata.password,
								oldPwd :this.mydata.originPass
							},
							success:(res)=>{
								console.log(res,'返回的数据')
								if(res.data.stat == 1){
									uni.showToast({
										title:'设置成功',
										icon:'success',
										success:()=>{
											setTimeout(function(){
												uni.navigateBack({
													delta:1
												})
											},2000);
										}
									})
								}else if(res.data.stat == 0){
									uni.showToast({
										title:res.data.message,
										icon:'none'
									})
								}else if(res.data.stat == 3){
									uni.showModal({
									    title: '提示',
									    content: '登录过期，请重新登录',
									    success: function (res) {
									        if (res.confirm) {
									            uni.navigateTo({
									            	url:'../myInit/myInit'
									            })
									        }
									    }
									});
								}
								
							}
						})
					} 
				})
			}
		},
		
		// 找回密码页面跳转
		forgetpass(){
			var check = 2
			uni.navigateTo({
				url:'../checkNumber/checkNumber?check='+check
			})
		}
	}
}
